.section {
    padding-bottom: 30px;
    .section-header {
        margin-bottom: 30px;
        margin-top: 30px;
    }
}

@mixin flex-layout {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
}

@mixin flex-row-layout {
    @include flex-layout();
    flex-direction: row;
    @media screen and (max-width: $mobile-max-width) {
        flex-direction: column;
    }
}

@mixin align-center {
    align-items: center;
    align-self: auto;
    text-align: center;
    justify-content: center;
}
